const componentDictionary = [
    { name: 'calories', label: ',' },
    { name: 'proteins', label: ',' },
    { name: 'fat', label: ',' },
    { name: 'carbohydrates', label: ',' },
];
export function getComponents(current) {
    return componentDictionary
        .map(({ label, name }, i) => ({
        id: `ingredients-${current[name]}-${i}`,
        name: label,
        value: current ? current[name] : '-',
    }));
}
